export function leapYear(year) {
  return year % 4 === 0 && year % 100 !== 0 || year % 400 === 0;
}
export function getMonthLength(year, month) {
  return new Date(year, month + 1, 0).getDate();
}
export var second = 1000;
export var minute = 60000;
export var hour = 3600000;
export var day = 86400000;
export var week = 604800000;
export function toSeconds(milliseconds) {
  return milliseconds / second;
}
export function toMinutes(milliseconds) {
  return milliseconds / minute;
}
export function toHours(milliseconds) {
  return milliseconds / hour;
}
export function toDays(milliseconds) {
  return milliseconds / day;
}
export function toWeeks(milliseconds) {
  return milliseconds / week;
}
export function getTimeBetween(begin, end) {
  return end.getTime() - begin.getTime();
}